import React from 'react';
import './GoodsList.scss';

type Props = {
  goods: string[]
};

export const GoodsList: React.FC<Props> = ({ goods }) => {
  return (
    <>
      <ul className="list">
        <h2 className="list__title">Goods List:</h2>
        {goods.map(good => (
          <li key={good} className="list__item">
            <div>{good}</div>
            <div>{`Length: ${good.length}`}</div>
          </li>
        ))}
      </ul>
    </>
  );
};
